@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
@import url(https://use.fontawesome.com/releases/v5.15.4/css/all.css);

* {
    margin: 0;
    padding: 0
}

.address {
    margin-bottom: 0;
}

.MuiButton-label {
    color: white;
}

.MuiButtonBase-root.MuiButton-root {
    background-color: #3a15c0;
}

.MuiButtonBase-root.MuiButton-root.Mui-disabled {
    background-color: #473b70;
}


.mint-container {
    background-color: #e3e3e3e3;
    border-radius: 3rem;
}

.redes .black {
    color: #000
}

.sneak-show {
    text-align: right;
}

.MuiPaper-root.MuiDialog-paper .MuiDialogTitle-root {
    background-color: #dddddd;
}

@media only screen and (max-width: 479px) {
    .nav2 {
        position:inherit
    }

    .parent {
        display: block
    }

    #logo {
        width: 4rem;
        height: 2.5rem;
        padding: 0
    }

    .div2 {
        grid-area: 1/1/4/4;
        background-image: url(images/background.png);
        background-blend-mode: soft-light;
        background-color: rgba(0,0,0,.432);
        padding: 3rem 0
    }

    .txt {
        text-shadow: #000;
        display: block;
        margin: 10rem 25rem 15rem 15rem;
        color: #fff;
        text-align: center;
        text-decoration: none;
        font-family: 'Anton', sans-serif;
        margin: 15rem 0 17.5rem
    }

    .txt h1 {
        font-size: 2rem;
        text-shadow: 3px 3px #000
    }

    .txt h2 {
        font-size: 1rem;
        text-shadow: 3px 3px #000
    }

    .btn1 {
        font-size: 3.5rem;
        display: block;
        margin: 7rem 35rem;
        padding: 1.5rem 4rem;
        border: 5px solid #585858;
        background-color: #181818;
        color: #fff;
        text-align: center;
        text-decoration: none;
        border-radius: 3rem
    }

    #logotw {
        max-height: 1rem;
        padding: auto
    }

    #logods {
        width: 1rem;
        height: 1rem
    }

    .div3 {
        padding: 2rem 0 7rem;
        background-color: #2e0016
    }

    .road {
        color: #000;
        text-align: center
    }

    #cards {
        display: flex;
        flex-direction: initial;
        flex-wrap: wrap;
        margin: 0 4rem;
        justify-content: center
    }

    .card1 {
        border-radius: 2rem;
        padding: 1rem;
        margin: 1rem;
        background-color: #680032;
        max-width: 20rem
    }

    .road h1 {
        font-size: 3rem;
        color: #fff;
        padding: 1rem;
        margin: 0 0 2rem;
        font-family: 'Anton', sans-serif
    }

    .road h2 {
        color: #fff;
        font-size: 1.2rem
    }

    .road p {
        font-size: .9rem;
        color: #fff;
        padding: auto;
        margin: auto
    }

    .div4 {
        padding: 2rem 0 3rem;
        background-color: #001b2b
    }

    .faq h1 {
        font-size: 4rem;
        color: #fff;
        text-align: center;
        margin: 5rem 0;
        font-family: 'Anton', sans-serif
    }

    .faq h2 {
        font-size: 1.2rem;
        color: #fff
    }

    .faq p {
        font-size: 1rem;
        color: #fff
    }

    .cards2 {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        margin: 1rem 3rem;
        justify-content: center
    }

    .card2 {
        border-radius: 1rem;
        padding: 1rem;
        margin: .3rem 0;
        background-color: #004068
    }

    .div5 {
        background-color: #000;
        text-align: center
    }

    .footer {
        font-size: 2rem;
        display: inline-block;
        background: #000;
        color: #fff;
        text-decoration: none;
        margin-top: 1rem
    }

    .footer ul {
        display: inline-block;
        list-style: none;
        text-decoration: none;
        color: #fff;
        margin-left: auto;
        margin-right: 1rem
    }

    .redes {
        display: inline-block
    }

    .redes a {
        color: #fff
    }

    .redes a:hover {
        color: grey
    }

    .redes2 {
        display: inline-block;
        font-size: 4rem;
        margin: 1.5rem
    }

    .redes2 a {
        color: #fff
    }

    .redes2 a:hover {
        color: #585858
    }
}

